/* ====================================== Content ======================================  */

html,
body {
  height: 100%;
}

/* default sizing for desktop */
body {
  display: flex;
  flex-direction: column;
  align-items: center;

  /* solves  Flash Of Unstyled Content (FOUC)*/
  /* html body style "opacity: 0; */
  opacity: 1 !important;

  width: 1200px;
  margin: auto;
  overflow-x: hidden;

  background-color: var(--main-color);
  font-family: "muktaregular", Arial, Helvetica, sans-serif;

  > * {
    padding: calc(10 * var(--padding)) var(--padding) 0;
  }
}

/* ================== main ================== */

/* ===== header ===== */
header {
  background-color: transparent;
  position: relative;
}

.img-name-wrapper {
  float: left;
  position: relative;
  z-index: 1;

  margin: 0 var(--spacing) 0 0;

  height: calc(var(--headshot-height) - 100px);
  width: var(--headshot-width);

  img {
    box-shadow: 5px 5px 5px black;

    position: absolute;
    bottom: 0px;
    right: 0px;

    min-height: var(--headshot-height);
    min-width: var(--headshot-width);

    animation: main-appear var(--time3) forwards;
    animation-delay: var(--delay3);
  }
}

.name-wrapper {
  font-family: bebas_neueregular;
  font-size: 5.75rem;
  color: var(--main-color);

  position: absolute;
  bottom: 45px;
  left: 8px;

  .last-name {
    color: red;

    position: absolute;
    top: 75px;
    right: 0px;

    animation: name-slide-in var(--time1) backwards;
    animation-delay: var(--delay1);
  }
}

.animated-background {
  background-color: var(--sec-color);

  height: 50rem;
  width: 450rem;
  transform: rotate(var(--bg-angle));
  transform-origin: bottom left;

  position: absolute;
  bottom: -12rem;
  left: -170rem;

  z-index: -1;

  animation: animate-background var(--time2) linear backwards;
  animation-delay: var(--delay2);
}

.about-wrapper {
  padding: var(--padding);
  position: relative;

  background-color: var(--main-color);
  box-shadow: 5px 5px 5px black;

  animation:
    main-appear var(--time4) backwards,
    main-slide-up var(--time4) backwards;
  animation-delay: var(--delay4);
}

.about-info-wrapper {
  p {
    font-size: large;
  }
}

.about-icons-wrapper {
  display: flex;
  justify-content: end;
  gap: var(--spacing);

  padding-top: 5rem;
}

/* ===== section ===== */

section {
  margin-top: 10rem;

  animation:
    main-appear var(--time4) backwards,
    main-slide-up var(--time4) backwards;
  animation-delay: var(--delay4);
}

.cards-wrapper {
  display: grid;
  grid-template-columns: repeat(3, auto);
  justify-content: space-between;
  gap: calc(2 * var(--spacing));
}

.card {
  width: var(--headshot-width);

  .project-pic {
    height: var(--headshot-width);

    &.hovered {
      background-image: none !important;
      background-color: white !important;
    }

    .project-pic-title {
      height: var(--headshot-width);

      color: transparent;

      -webkit-background-clip: text;
      background-clip: text;

      font-family: bebas_neueregular;

      &.hovered {
        color: rgba(255, 0, 0, 0.5);
      }
    }
  }

  .card-info-wrapper {
    padding: var(--padding) calc(0.5 * var(--padding));

    .card-info-top-line {
      display: flex;
      justify-content: space-between;

      h4 {
        font-size: 1.5rem;
      }

      .card-icons-wrapper {
        display: flex;
        align-items: center;
        gap: calc(0.5 * var(--spacing));
      }

      img.icon {
        height: 1.5rem;
        width: 1.5rem;
        min-height: 1.5rem;
        min-width: 1.5rem;
      }
    }
  }
}

