/* ================== footer ================== */

footer {
  * {
    text-align: start;
  }

  flex-grow: 1;

  padding: var(--padding) 0;

  width: 100vw;
  background-color: var(--sec-color);
  color: var(--main-color);
  font-size: large;

  div.wrapper {
    margin: auto;
    max-width: 1200px;
    padding-left: var(--padding);

    display: grid;
    grid-template-columns: 1fr 2fr;
    justify-content: space-between;

    text-align: center;

    .address,
    .phone,
    .email {
      margin-top: var(--spacing);

      display: flex;
      gap: var(--spacing);
      align-items: center;
    }

    picture {
      display: flex;
      img {
        width: 715px;

        margin-left: auto;
        align-self: center;
      }
    }
    .icon {
      filter: invert(100%);
    }

    .about-icons-wrapper {
      justify-content: start;

      padding-top: calc(var(--padding) * 2);
    }
  }
}

/* ====================================== Dynamic sizing ======================================  */

/* tablet */
@media (max-width: 1199px) {
  body {
    width: 615px;
  }

  .img-name-wrapper {
    width: calc(var(--headshot-width) - 100px);

    .name-wrapper {
      bottom: 380px;
      left: 250px;
    }
  }

  .about-info-wrapper {
    padding-top: 7rem;

    h3 {
      text-align: center;
    }
  }

  .animated-background {
    bottom: 20rem;
  }

  .cards-wrapper {
    grid-template-columns: repeat(2, auto);
    justify-content: center;
  }

  footer {
    div.wrapper {
      grid-template-columns: auto auto;
      max-width: calc(682px + 32px + 32px);

      picture img {
        min-width: var(--headshot-width);
      }
    }
  }
}

/* mobile */
@media (max-width: 740px) {
  body {
    width: calc(var(--headshot-width) + 0rem);
  }

  main {
    padding-top: calc(2 * var(--padding));
  }

  .img-name-wrapper {
    float: unset;

    width: var(--headshot-width);
    height: var(--headshot-height);

    margin-bottom: 1rem;
    margin-left: auto;
    margin-right: auto;

    img {
      box-shadow: none;
    }
    .name-wrapper {
      bottom: 45px;
      left: 8px;
    }
  }
  .about-wrapper {
    box-shadow: none;

    .about-info-wrapper {
      padding-top: 0;
    }

    .about-icons-wrapper {
      justify-content: center;

      padding-top: var(--padding);
    }
  }

  .animated-background {
    bottom: 22rem;
  }

  .cards-wrapper {
    grid-template-columns: repeat(1, auto);
  }

  footer {
    div.wrapper {
      grid-template-columns: 1fr 1fr;
    }
  }

  footer {
    padding-bottom: 0;

    div.wrapper {
      .contact-info {
        padding: 0 var(--padding);
        margin-bottom: calc(5 * var(--spacing));
      }

      padding: 0;

      display: block;
      max-width: var(--headshot-width);

      picture img {
        min-width: var(--headshot-width);
      }
      .about-icons-wrapper {
        justify-content: center;
      }
    }
  }
}

/* ====================================== Catch All ======================================  */

img.icon {
  --icon-size: 2.5rem;

  border: none;
  background: none;

  width: var(--icon-size);
  height: var(--icon-size);

  transform: translateY(0);

  transition: transform 0.33s;
}

.icon-link {
  &:hover .icon {
    transform: translateY(-5px);

    transition: transform 0.1s;
  }
}

h3 {
  font-size: 2rem;
}

/* ====================================== Animation ======================================  */

@keyframes animate-background {
  from {
    transform: rotate(var(--bg-angle)) translateX(600rem);
  }
  to {
    transform: rotate(var(--bg-angle)) translateX(0rem);
  }
}

@keyframes main-appear {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}

@keyframes main-slide-up {
  from {
    transform: translateY(50px);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes name-slide-in {
  from {
    transform: translateX(-100vw);
  }
  to {
    transform: translateX(0);
  }
}
