B,mBAAmB;IACnB,iBAAiB;IACjB,kBAAkB;;IAElB;MACE,gBAAgB;IAClB;IACA;MACE,YAAY;MACZ,SAAS;IACX;EACF;EACA;IACE,gBAAgB;;IAEhB;MACE,cAAc;IAChB;;IAEA;MACE,uBAAuB;;MAEvB,2BAA2B;IAC7B;EACF;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,sCAAsC;EACxC;;EAEA;IACE;MACE,8BAA8B;IAChC;EACF;;EAEA;IACE,iBAAiB;;IAEjB;MACE;QACE,yBAAyB;QACzB,uCAAuC;MACzC;;MAEA,UAAU;;MAEV,cAAc;MACd,gCAAgC;;MAEhC;QACE,gCAAgC;MAClC;MACA;QACE,uBAAuB;MACzB;IACF;EACF;AACF;;AAEA,6FAA6F;;AAE7F;EACE,mBAAmB;;EAEnB,YAAY;EACZ,gBAAgB;;EAEhB,uBAAuB;EACvB,wBAAwB;;EAExB,wBAAwB;;EAExB,2BAA2B;AAC7B;;AAEA;EACE;IACE,2BAA2B;;IAE3B,0BAA0B;EAC5B;AACF;;AAEA;EACE,eAAe;AACjB;;AAEA,6FAA6F;;AAE7F;EACE;IACE,qDAAqD;EACvD;EACA;IACE,mDAAmD;EACrD;AACF;;AAEA;EACE;IACE,WAAW;EACb;EACA;IACE,aAAa;EACf;AACF;;AAEA;EACE;IACE,2BAA2B;EAC7B;EACA;IACE,wBAAwB;EAC1B;AACF;;AAEA;EACE;IACE,6BAA6B;EAC/B;EACA;IACE,wBAAwB;EAC1B;AACF","sourcesContent":["/* style.css */\n\n/* ====================================== Imports ======================================  */\n\n@import url(\"./reset.css\");\n\n@font-face {\n  font-family: \"bebas_neueregular\";\n  src:\n    url(\"./fonts/Bebas_Neue/bebasneue-regular-webfont.woff2\") format(\"woff2\"),\n    url(\"./fonts/Bebas_Neue/bebasneue-regular-webfont.woff\") format(\"woff\"),\n    url(\"./fonts/Bebas_Neue/BebasNeue-Regular.ttf\") format(\"truetype\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: \"muktaregular\";\n  src:\n    url(\"./fonts/Mukta/mukta-regular-webfont.woff2\") format(\"woff2\"),\n    url(\"./fonts/Mukta/mukta-regular-webfont.woff\") format(\"woff\"),\n    url(\"./fonts/Mukta/Mukta-Regular.ttf\") format(\"truetype\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n/* ====================================== Global ======================================  */\n\n:root {\n  /* general variables */\n  --main-color: white;\n  --sec-color: black;\n  --spacing: 1rem;\n  --padding: 1rem;\n\n  /* specific variables */\n  --bg-angle: -3deg;\n  --headshot-width: 341px;\n  --headshot-height: 512px;\n\n  /* animation timing */\n  --delay1: 0.5s;\n  --time1: 0.5s;\n  --delay2: calc(var(--delay1) + var(--time1) - 0.25s);\n  --time2: 1s;\n  --delay3: calc(var(--delay2) + var(--time2) + 0.5s);\n  --time3: 1s;\n  --delay4: calc(var(--delay3) + var(--time3) - 0.25s);\n  --time4: 1s;\n\n  /* testing */\n  /* --delay1: 0s;\n  --time1: 0s;\n  --delay2: 0s;\n  --time2: 0s;\n  --delay3: 0s;\n  --time3: 0s;\n  --delay4: 0s;\n  --time4: 0s; */\n\n  /* debugging */\n  * {\n    /* outline: solid red 1px; */\n  }\n}\n\n/* ====================================== Content ======================================  */\n\nhtml,\nbody {\n  height: 100%;\n}\n\n/* default sizing for desktop */\nbody {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n\n  /* solves  Flash Of Unstyled Content (FOUC)*/\n  /* html body style \"opacity: 0; */\n  opacity: 1 !important;\n\n  width: 1200px;\n  margin: auto;\n  overflow-x: hidden;\n\n  background-color: var(--main-color);\n  font-family: \"muktaregular\", Arial, Helvetica, sans-serif;\n\n  > * {\n    padding: calc(10 * var(--padding)) var(--padding) 0;\n  }\n}\n\n/* ================== main ================== */\n\n/* ===== header ===== */\nheader {\n  background-color: transparent;\n  position: relative;\n}\n\n.img-name-wrapper {\n  float: left;\n  position: relative;\n  z-index: 1;\n\n  margin: 0 var(--spacing) 0 0;\n\n  height: calc(var(--headshot-height) - 100px);\n  width: var(--headshot-width);\n\n  img {\n    box-shadow: 5px 5px 5px black;\n\n    position: absolute;\n    bottom: 0px;\n    right: 0px;\n\n    min-height: var(--headshot-height);\n    min-width: var(--headshot-width);\n\n    animation: main-appear var(--time3) forwards;\n    animation-delay: var(--delay3);\n  }\n}\n\n.name-wrapper {\n  font-family: bebas_neueregular;\n  font-size: 5.75rem;\n  color: var(--main-color);\n\n  position: absolute;\n  bottom: 45px;\n  left: 8px;\n\n  .last-name {\n    color: red;\n\n    position: absolute;\n    top: 75px;\n    right: 0px;\n\n    animation: name-slide-in var(--time1) backwards;\n    animation-delay: var(--delay1);\n  }\n}\n\n.animated-background {\n  background-color: var(--sec-color);\n\n  height: 50rem;\n  width: 450rem;\n  transform: rotate(var(--bg-angle));\n  transform-origin: bottom left;\n\n  position: absolute;\n  bottom: -12rem;\n  left: -170rem;\n\n  z-index: -1;\n\n  animation: animate-background var(--time2) linear backwards;\n  animation-delay: var(--delay2);\n}\n\n.about-wrapper {\n  padding: var(--padding);\n  position: relative;\n\n  background-color: var(--main-color);\n  box-shadow: 5px 5px 5px black;\n\n  animation:\n    main-appear var(--time4) backwards,\n    main-slide-up var(--time4) backwards;\n  animation-delay: var(--delay4);\n}\n\n.about-info-wrapper {\n  p {\n    font-size: large;\n  }\n}\n\n.about-icons-wrapper {\n  display: flex;\n  justify-content: end;\n  gap: var(--spacing);\n\n  padding-top: 5rem;\n}\n\n/* ===== section ===== */\n\nsection {\n  margin-top: 10rem;\n\n  animation:\n    main-appear var(--time4) backwards,\n    main-slide-up var(--time4) backwards;\n  animation-delay: var(--delay4);\n}\n\n.cards-wrapper {\n  display: grid;\n  grid-template-columns: repeat(3, auto);\n  justify-content: space-between;\n  gap: calc(2 * var(--spacing));\n}\n\n.card {\n  width: var(--headshot-width);\n\n  .project-pic {\n    height: var(--headshot-width);\n\n    &.hovered {\n      background-image: none !important;\n      background-color: white !important;\n    }\n\n    .project-pic-title {\n      height: var(--headshot-width);\n\n      color: transparent;\n\n      -webkit-background-clip: text;\n      background-clip: text;\n\n      font-family: bebas_neueregular;\n\n      &.hovered {\n        color: rgba(255, 0, 0, 0.5);\n      }\n    }\n  }\n\n  .card-info-wrapper {\n    padding: var(--padding) calc(0.5 * var(--padding));\n\n    .card-info-top-line {\n      display: flex;\n      justify-content: space-between;\n\n      h4 {\n        font-size: 1.5rem;\n      }\n\n      .card-icons-wrapper {\n        display: flex;\n        align-items: center;\n        gap: calc(0.5 * var(--spacing));\n      }\n\n      img.icon {\n        height: 1.5rem;\n        width: 1.5rem;\n        min-height: 1.5rem;\n        min-width: 1.5rem;\n      }\n    }\n  }\n}\n\n/* ==== per project specs ==== */\n\n/* #1 */\n.card#project1 {\n  .project-pic {\n    background-image: url(./images/harakirijpg.jpg);\n    background-position: center;\n    background-size: cover;\n  }\n\n  .project-pic-title {\n    text-align: end;\n    font-size: 13rem;\n    line-height: 10rem;\n    background-image: url(./images/harakirijpg.jpg);\n    background-position: center;\n    background-size: cover;\n  }\n}\n\n/* #2 */\n.card#project2 {\n  .project-pic {\n    background-image: url(./images/yojimbo.jpg);\n    background-position: center;\n    background-size: cover;\n  }\n\n  .project-pic-title {\n    text-align: center;\n    font-size: 13rem;\n    line-height: 10rem;\n    background-image: url(./images/yojimbo.jpg);\n    background-position: center;\n    background-size: cover;\n  }\n}\n\n/* #3 */\n.card#project3 {\n  .project-pic {\n    background-image: url(./images/swift.jpg);\n    background-position: center;\n    background-size: cover;\n  }\n\n  .project-pic-title {\n    text-align: start;\n    font-size: 4.5rem;\n    line-height: 4rem;\n    background-image: url(./images/swift.jpg);\n    background-position: center;\n    background-size: cover;\n\n    *:first-child {\n      font-size: 11rem;\n      line-height: 8.5rem;\n    }\n  }\n}\n\n/* #4 */\n.card#project4 {\n  .project-pic {\n    background-image: url(./images/seven.jpg);\n    background-position: center;\n    background-size: cover;\n  }\n\n  .project-pic-title {\n    text-align: end;\n    font-size: 8rem;\n    line-height: 7rem;\n\n    background-image: url(./images/seven.jpg);\n    background-position: center;\n    background-size: cover;\n\n    *:first-child {\n      font-size: 4rem;\n      line-height: 4rem;\n    }\n    *:nth-child(2) {\n      font-size: 11rem;\n      line-height: 8rem;\n    }\n  }\n}\n\n/* #5 */\n.card#project5 {\n  .project-pic {\n    background-image: url(./images/sheihara.jpg);\n    background-position: center;\n    background-size: cover;\n  }\n\n  .project-pic-title {\n    text-align: center;\n    font-size: 11rem;\n    line-height: 8rem;\n\n    background-image: url(./images/sheihara.jpg);\n    background-position: center;\n    background-size: cover;\n\n    *:first-child {\n      font-size: 4rem;\n      line-height: 4rem;\n    }\n  }\n}\n\n/* #6 */\n.card#project6 {\n  .card-icons-wrapper {\n    align-items: start;\n    margin-top: calc(0.5 * var(--spacing));\n  }\n\n  .project-pic {\n    background-image: url(./images/kagemusha.jpg);\n    background-position: left 0% top 15%;\n    background-size: cover;\n  }\n\n  .project-pic-title {\n    text-align: start;\n    font-size: 10rem;\n    line-height: 7.5rem;\n\n    background-image: url(./images/kagemusha.jpg);\n    background-position: left 0% top 15%;\n    background-size: cover;\n\n    *:last-child {\n      font-size: 4rem;\n      line-height: 3rem;\n    }\n  }\n}\n\n/* ================== footer ================== */\n\nfooter {\n  * {\n    text-align: start;\n  }\n\n  flex-grow: 1;\n\n  padding: var(--padding) 0;\n\n  width: 100vw;\n  background-color: var(--sec-color);\n  color: var(--main-color);\n  font-size: large;\n\n  div.wrapper {\n    margin: auto;\n    max-width: 1200px;\n    padding-left: var(--padding);\n\n    display: grid;\n    grid-template-columns: 1fr 2fr;\n    justify-content: space-between;\n\n    text-align: center;\n\n    .address,\n    .phone,\n    .email {\n      margin-top: var(--spacing);\n\n      display: flex;\n      gap: var(--spacing);\n      align-items: center;\n    }\n\n    picture {\n      display: flex;\n      img {\n        width: 715px;\n\n        margin-left: auto;\n        align-self: center;\n      }\n    }\n    .icon {\n      filter: invert(100%);\n    }\n\n    .about-icons-wrapper {\n      justify-content: start;\n\n      padding-top: calc(var(--padding) * 2);\n    }\n  }\n}\n\n/* ====================================== Dynamic sizing ======================================  */\n\n/* tablet */\n@media (max-width: 1199px) {\n  body {\n    width: 615px;\n  }\n\n  .img-name-wrapper {\n    width: calc(var(--headshot-width) - 100px);\n\n    .name-wrapper {\n      bottom: 380px;\n      left: 250px;\n    }\n  }\n\n  .about-info-wrapper {\n    padding-top: 7rem;\n\n    h3 {\n      text-align: center;\n    }\n  }\n\n  .animated-background {\n    bottom: 20rem;\n  }\n\n  .cards-wrapper {\n    grid-template-columns: repeat(2, auto);\n    justify-content: center;\n  }\n\n  footer {\n    div.wrapper {\n      grid-template-columns: auto auto;\n      max-width: calc(682px + 32px + 32px);\n\n      picture img {\n        min-width: var(--headshot-width);\n      }\n    }\n  }\n}\n\n/* mobile */\n@media (max-width: 740px) {\n  body {\n    width: calc(var(--headshot-width) + 0rem);\n  }\n\n  main {\n    padding-top: calc(2 * var(--padding));\n  }\n\n  .img-name-wrapper {\n    float: unset;\n\n    width: var(--headshot-width);\n    height: var(--headshot-height);\n\n    margin-bottom: 1rem;\n    margin-left: auto;\n    margin-right: auto;\n\n    img {\n      box-shadow: none;\n    }\n    .name-wrapper {\n      bottom: 45px;\n      left: 8px;\n    }\n  }\n  .about-wrapper {\n    box-shadow: none;\n\n    .about-info-wrapper {\n      padding-top: 0;\n    }\n\n    .about-icons-wrapper {\n      justify-content: center;\n\n      padding-top: var(--padding);\n    }\n  }\n\n  .animated-background {\n    bottom: 22rem;\n  }\n\n  .cards-wrapper {\n    grid-template-columns: repeat(1, auto);\n  }\n\n  footer {\n    div.wrapper {\n      grid-template-columns: 1fr 1fr;\n    }\n  }\n\n  footer {\n    padding-bottom: 0;\n\n    div.wrapper {\n      .contact-info {\n        padding: 0 var(--padding);\n        margin-bottom: calc(5 * var(--spacing));\n      }\n\n      padding: 0;\n\n      display: block;\n      max-width: var(--headshot-width);\n\n      picture img {\n        min-width: var(--headshot-width);\n      }\n      .about-icons-wrapper {\n        justify-content: center;\n      }\n    }\n  }\n}\n\n/* ====================================== Catch All ======================================  */\n\nimg.icon {\n  --icon-size: 2.5rem;\n\n  border: none;\n  background: none;\n\n  width: var(--icon-size);\n  height: var(--icon-size);\n\n  transform: translateY(0);\n\n  transition: transform 0.33s;\n}\n\n.icon-link {\n  &:hover .icon {\n    transform: translateY(-5px);\n\n    transition: transform 0.1s;\n  }\n}\n\nh3 {\n  font-size: 2rem;\n}\n\n/* ====================================== Animation ======================================  */\n\n@keyframes animate-background {\n  from {\n    transform: rotate(var(--bg-angle)) translateX(600rem);\n  }\n  to {\n    transform: rotate(var(--bg-angle)) translateX(0rem);\n  }\n}\n\n@keyframes main-appear {\n  from {\n    opacity: 0%;\n  }\n  to {\n    opacity: 100%;\n  }\n}\n\n@keyframes main-slide-up {\n  from {\n    transform: translateY(50px);\n  }\n  to {\n    transform: translateY(0);\n  }\n}\n\n@keyframes name-slide-in {\n  from {\n    transform: translateX(-100vw);\n  }\n  to {\n    transform: translateX(0);\n  }\n}\n"],"sourceRoot":""}]);
